
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.user-info:before, .user-info:after {
  content: '';
  position: absolute;
  bottom: -63%;
  width: 100%;
  height: 70%;
  background: #e6e6e6;
  left: auto;
  right: -25%;
  transform: rotate(-8deg);
  -webkit-transform: rotate(-8deg);
  -moz-transform: rotate(-8deg);
  -o-transform: rotate(-8deg);
}
.user-info:after {
  left: -25%;
  right: auto;
  transform: rotate(8deg);
  -webkit-transform: rotate(8deg);
  -moz-transform: rotate(8deg);
  -o-transform: rotate(8deg);
}

.main-content section {
  border-bottom: solid 1px #e6e6e6;
  padding-bottom: 15px;
  margin: 15px;
}
.mb-15 {
  margin-bottom: 15px;
}

.mb-10 {
  margin-bottom: 10px;
}
.mb-5 {
  margin-bottom: 5px;
}

.title {
  font-size: 1.5em;
  font-weight: 400;
  margin-bottom: 1.571em;
  color: #757575;
}
.space-between {
  justify-content: space-between!important;
}

.d-flex {
  display: flex !important;
}
.sub-title {
  font-size: 1em;
  font-weight: 600;
  margin-bottom: 5px;
  margin-right: 15px;
}
.translate {
  transition: all 0.3s;
}

.translate:hover {
  transform: translateY(-5px);
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 6px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.MuiSlider-track, .MuiSlider-rail {
  height: 5px!important;
}

 .product-categories {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 60vh;
  max-height: 450px;
}

 .product-categories ul {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  list-style: none;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

 .product-categories ul li {
  position: relative;
  height: 100%;
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid white;
  transition: all 0.7s;
  margin: 0 5px;
  background-image: linear-gradient(90deg, #f42c37 61%, #ff4e59 100%);
}

 .product-categories ul li:hover {
  width: 100% !important;
}

 .product-categories ul li span {
  display: block;
  position: absolute;
  bottom: 0;
  width: 50%;
  color: white;
  white-space: nowrap;
  padding: 20px;
}

 .product-categories ul li img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}